#mainArea {
  margin-left: 180px;
  margin-top: 50px;
  padding: 30px;
  background-color: #f3f3f3;
  min-height: calc(100vh - 100px);
}


#tabs {
  width: 100%;
  overflow-x: scroll;
  display: flex;
}
.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  background-color: #f8f8f8;
  padding: 10px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  cursor: pointer;
}

.active {
  background-color: white;
}

.menu-icon {
  margin-right: 20px;
}

.menu-icon:hover {
  color: red;
}

.input-field {
  border-radius: 7px;
  border: 2px solid black;
  padding: 0 10px;
}
.input-field:focus {
  outline: none;
  border: 2px solid red;
}
